@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Roboto', sans-serif;
}

 h1 {
   padding-bottom: 30px;
  }

  /* /////////// NavBar component /////// */
.navbar {
  background-color: grey;
  height: 3rem;
  font-size: 32px;
  color: white;
  padding: 32px 32px;
}
#logo {
  height: 32px;
}

/* /////////// Login component /////// */

.inputForm {
  font-size: 1.4em;
  color: black;
  outline: 0;
  border-color: lightgrey;
  margin: auto auto;
  width: 100%;
  padding-top: 2em;
  padding: 0.5em 0;
  border: none;
  border-bottom: 1px solid #eaeaea;
  padding-bottom: 0.1em;
}

.inputForm:focus {
  border-color: #76c9cf
}

#eyeIcon {
  color: lightgrey;
  font-size: 1.5em;
}

.container #eyeIcon{
  margin-left: -25px;
}

.log-form {
  width: 40%;
  min-width: 320px;
  max-width: 475px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 40em) {
  .log-form {
    width: 95%;
    position: relative;
    margin: 2.5% auto 0 auto;
    left: 0%;
    transform: translate(0%, 0%);
  }
}

.log-form input:focus {
  outline: none;
}

.log-form .btn {
  display: inline-block;
  background: #5bd3d3;
  border: 1px solid #5bd3d3;
  color: white;
  width: 100%;
  height: 3em;
  font-size: 1em;
  margin-right: 0.5em;
  border-radius: 3px;
  box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 0.2);
}
.log-form .btn:hover {
  background: #23cad5;
}

.log-form .btn:focus {
  outline: none;
}
.log-form .forgot {
  color: grey;
  position: relative;
  text-decoration: underline;
  font-size: 0.75em;
  margin: 0;
  padding: 9px;
  margin-bottom: 9px;;
  float: right;
}

.label {
  color: grey;
  line-height: 0.5em;
  position: relative;
  font-size: 16px;
  margin: 0;
  padding: 0;
}

.error {
  color: red;
  font-size: 0.75em;
  text-align: left;
  height: 1em;
}

body {
  font-family: 'open sans', sans-serif;
  font-size: 16px;
  color: #505050;
  background-color: #c7c2c2;
}
/* form {
  margin: 0px;
} */

input {
  height: 37px;
  border: 0.1px solid grey;
  margin-left: 16px;
  /* margin-top:  16px; */
  background-color: white;
  line-height: 400;
  font-size: 16px;
}
button {
  height: 40px;
}

.page {
  justify-content: center;
}

.main {
  width: 100%;
  max-width: 912px;
  box-sizing: border-box;
  top: 0px;
  left: 0px;
}

.tableContainer {
  width: 100%;
  max-width: 912px;
  box-sizing: border-box;
  margin: auto;

}

#participantTable {
  background-color: white;
}

table {
  width: 100%;
  border-collapse: collapse;
}

thead th {
  text-align: left;
  vertical-align: top;
  border-bottom: 1px solid #ccc;
  height: 30.5px;
  font-size: 14px;
  line-height: 16px;
  padding-left: 16px;
  padding-top: 16px;
  color: #757575;
  font-weight: bold;
  /* font-weight: 500!important; */
}

thead button {
  border: 0;
  border-radius: none;
  font-family: inherit;
  font-weight: 700;
  font-size: inherit;
  padding: 0.5em;
  /* margin-bottom: 1px; */
}

thead button.ascending::after {
  content: '↓';
  display: inline-block;
  margin-left: 1em;
}

thead button.descending::after {
  content: '↑';
  display: inline-block;
  margin-left: 1em;
}

tbody td {
  border-bottom: 0.1px solid #ccc;
  margin-bottom: 24px!important;
  max-height: 71.5px;
  padding: 0px 0px;
}

td {
  height: 71.5px;
}

.myInput {
  background-color: whitesmoke;
}

.cellName {
  min-width: 150px;
  padding-left: 24px!important;
}
.cellEmail {
  min-width: 280px;
  padding-left: 24px!important;
}
.cellPhone{
  max-width: 120px;
  padding-left: 24px!important;
}



.icon-only {
  /* min-width: 200px; */
  max-width: 158px;
  display: flex;
  flex-direction: row;
  justify-content: right;
  margin-right: 16px;
  margin-left: 16px;
}
/* //////////////// AddNew Component //////////// */

#Adding {
  display: flex;
  justify-content: space-between;
  padding: 16px 0px;
  background-color: #FEFEFE;
  margin-bottom: 10px;
  max-height: 40px;
}

/* //////////////// Editing Component //////////// */
#editParticipant {
  display: flex;
  max-width: 192px;
  float: left;
  margin: 16px 16px;
}


.myButton {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  color:#909090;
  height: 24px;
  width: 24px;;
  margin-left: 16px;
}

.cancelButton {
  color: #07f;
  background-color: #dad5d5;
  font-size: 16px;
  font-weight: 500;
  height: 40px;
  border: 0px solid transparent;
  margin-right: 2px;
}
.saveButton {
  color: #ffffff;
  background-color: #07f;
  font-size: 16px;
  font-weight: 500;
  height: 40px;
  border: 0px solid transparent;
  margin-left: 2px;
}

.addButton {
  color: grey;
  background-color: #ddd;
  font-size: 16px;
  /* font-weight: 500!important; */
  font-weight: bold;
  height: 40px;
  border: 1px solid grey;
  margin-right: 2px;
  justify-content: right;
  margin-right: 16px;
  font-weight: 500;
  border: 0px solid transparent;
  float: right;
  padding-top: 1px;
}

.cellAction {
  width: 158px!important;
}

